// 与AI对战
const startAIGame = () => {
  uni.showToast({
    title: 'AI对战功能开发中',
    icon: 'none',
    duration: 2000
  });
};

// 局域网模式
const startLocalGame = () => {
  // 切换到游戏页面，显示底部导航栏
  uni.switchTab({
    url: '/pages/home/home'
  });
};

// 退出游戏
const exitGame = () => {
  uni.showModal({
    title: '退出游戏',
    content: '确定要退出游戏吗？',
    success: (result) => {
      if (result.confirm) {
        // 简化退出逻辑，使用uni的退出方法
        wx.exitMiniProgram();
      }
    }
  });
};
